import React from 'react';
import {RemoveScroll} from 'react-remove-scroll';
import OleSidebar from './oleSideBar';
import './oleBody.css';

export default class OleBody extends React.Component {
  
  render() {
  const toggleSidebar = this.props.toggleSidebar;
  let sidebar;

  if(toggleSidebar) {
    sidebar = <RemoveScroll>
                <OleSidebar toggleSidebar={toggleSidebar} />
            </RemoveScroll>      
  } else {
    sidebar = <OleSidebar toggleSidebar={toggleSidebar} />;
  }

  return(
    <div className="container-fluid">
      <div className="row oleBody">
        {sidebar}
        <div className="col">
          <div className="">
            <h1 className="py-4 text-center">Content Page</h1>
              <h4>Default View:</h4>
              <p><strong>Desktop - </strong>Sidebar remains expanded and lists menu items. Content Page can take a separate layout for each content tpe.</p>
              <p><strong>Mobile - </strong>Sidebar remains collapsed. Entire screen real estate is available for content. When user clicks the togle button, the side bar pushes the content page to occupy the real estate and display menu options.</p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}